.layoutWrapper {
  width: 100%;
  margin: auto;
}
.layoutWrapper > .content {
  width: 100%;
  min-height: calc(100vh - 124px);
}
.logo {
  width: 158px;
  height: 40px;
  background: #fff;
  margin: 16px 24px 16px 15px;
  float: left;
  background-image: url(data:image/png;base64,);
  background-position: center;
  background-repeat: no-repeat;
}
.site-layout .site-header {
  background: #fff;
  height: 72px;
  padding: 0px !important;
  z-index: 100;
  width: 100%;
}
.content {
  background: #fff;
  padding: 24px;
  min-height: calc(100vh - 142px);
}
.sider {
  overflow: auto;
  height: 100vh;
  position: fixed !important;
  left: 0;
  background-color: #fff !important;
  z-index: 10;
}
aside .ant-menu {
  min-height: 100vh;
}
