html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  font-family: PingFangSC-Regular;
}
body h1,
body h2,
body h3,
body h4,
body h5,
body h6 {
  margin: 0;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
.display_flex {
  display: flex;
}
.flex_one {
  flex: 1 1;
}
.flex_center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.blue {
  color: #1890ff;
  text-decoration: underline;
  cursor: pointer;
}
.red {
  color: red !important;
}
.dasded {
  border-bottom: 1px dashed#000;
}
.justBlue {
  color: #1890ff;
  font-size: 16px;
}
.green {
  color: #A5DF00 !important;
}
.yellow {
  color: #fadb14 !important;
}
.ccc {
  color: #ccc;
}
.fs16 {
  font-size: 16px;
}
.fs18 {
  font-size: 18px;
}
.pointer {
  cursor: pointer;
}
.w90 {
  width: 90px;
}
.w80 {
  width: 80px;
}
.w70 {
  width: 70px;
}
.w60 {
  width: 60px;
}
.w162 {
  width: 162px;
}
.ant-layout-sider-children {
  min-height: calc(100vh - 178px);
}
.ant-typography kbd {
  cursor: pointer;
}
.ajax-loading {
  display: none;
}
.ajax-loading .loading {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 0 40px;
  height: 80px;
  line-height: 80px;
  background: rgba(0, 0, 0, 0.75);
  border-radius: 6px;
  text-align: center;
  z-index: 9999;
  font-size: 16px;
  color: #fff;
}
.ajax-loading .loading img {
  width: 32px;
  vertical-align: middle;
}
.ajax-loading .loading span {
  margin-left: 12px;
}
.ajax-loading .overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 9998;
  background: #ffffff;
  opacity: 0.1;
}
